%active-star {
  background-image: url("../images/star-active.svg");
}

.stars {
  display: flex;

  &__star {
    height: 16px;
    width: 16px;
    margin-right: 4px;
    background-image: url("../images/star.svg");
    background-position: center;
    background-repeat: no-repeat;

    &:last-child {
      margin-right: 0;
    }
  }

  @for $index from 1 through 5 {
    &--#{$index} &__star:nth-child(-n+#{$index}) {
      @extend %active-star;
    }
  }
}
